"use client"

import type React from "react"

import { useState, useCallback } from "react"
import { X, Download, Upload, CheckCircle } from "lucide-react"
import type { Document } from "@/types/document"
import { DocumentIcon } from "./document-icon"
import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"

interface DocumentPreviewModalProps {
  document: Document | null
  isOpen: boolean
  onClose: () => void
  showSigningPanel?: boolean
}

type UploadState = "idle" | "uploading" | "success"

export function DocumentPreviewModal({
  document,
  isOpen,
  onClose,
  showSigningPanel = false,
}: DocumentPreviewModalProps) {
  const [uploadState, setUploadState] = useState<UploadState>("idle")
  const [uploadedFileName, setUploadedFileName] = useState("")
  const [isDragOver, setIsDragOver] = useState(false)

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragOver(true)
  }, [])

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragOver(false)
  }, [])

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault()
    setIsDragOver(false)
    const files = e.dataTransfer.files
    if (files.length > 0) {
      handleFileUpload(files[0])
    }
  }, [])

  const handleFileUpload = (file: File) => {
    setUploadState("uploading")
    setUploadedFileName(file.name)
    // Simulate upload
    setTimeout(() => {
      setUploadState("success")
    }, 1500)
  }

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files
    if (files && files.length > 0) {
      handleFileUpload(files[0])
    }
  }

  const handleClose = () => {
    setUploadState("idle")
    setUploadedFileName("")
    onClose()
  }

  if (!isOpen || !document) return null

  const showSigning = showSigningPanel || document.status === "unsigned"

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/60" onClick={handleClose} />

      <div className="relative z-10 flex w-full max-w-5xl mx-4">
        {/* Main Content */}
        <div className={cn("bg-[#2a2a2a] rounded-l-xl flex-1 flex flex-col", !showSigning && "rounded-r-xl")}>
          {/* Header */}
          <div className="flex items-center justify-between px-6 py-4">
            <div className="flex items-center gap-3">
              <DocumentIcon />
              <div className="text-white">
                <div className="font-medium">{document.name}</div>
                <div className="text-sm text-gray-400">
                  {document.status === "signed" ? "Подписан" : "Не подписан"}
                  {document.date && ` • ${document.date}`} • {document.size}
                </div>
              </div>
            </div>
            <div className="flex items-center gap-2">
              {document.status === "signed" && (
                <Button variant="ghost" size="icon" className="text-white hover:bg-white/10">
                  <Download className="h-5 w-5" />
                </Button>
              )}
              <Button variant="ghost" size="icon" className="text-white hover:bg-white/10" onClick={handleClose}>
                <X className="h-5 w-5" />
              </Button>
            </div>
          </div>

          {/* Document Preview */}
          <div className="flex-1 flex items-center justify-center p-6">
            <div className="bg-white rounded-lg shadow-2xl w-full max-w-xl p-8">
              <h1 className="text-2xl font-bold text-center text-emerald-600 mb-6">Договор на участие</h1>

              <div className="space-y-4 text-sm">
                <section>
                  <h2 className="font-bold mb-2">1. ПРЕДМЕТ ДОГОВОРА</h2>
                  <p className="text-gray-600">
                    Настоящий договор заключен между Организатором соревнования и Участником и определяет условия
                    участия в спортивном мероприятии "Чемпионат России 2024".
                  </p>
                </section>

                <section>
                  <h2 className="font-bold mb-2">2. ПРАВА И ОБЯЗАННОСТИ СТОРОН</h2>
                  <div className="space-y-2">
                    <p className="font-medium">2.1. Организатор обязуется:</p>
                    <ul className="list-disc list-inside text-gray-600 pl-4 space-y-1">
                      <li>Обеспечить проведение соревнования в соответствии с регламентом</li>
                      <li>Предоставить необходимое оборудование и инвентарь</li>
                      <li>Обеспечить медицинское сопровождение мероприятия</li>
                    </ul>
                    <p className="font-medium mt-3">2.2. Участник обязуется:</p>
                    <ul className="list-disc list-inside text-gray-600 pl-4 space-y-1">
                      <li>Соблюдать правила и регламент соревнования</li>
                      <li>Пройти медицинское обследование и предоставить справку</li>
                      <li>Иметь страховой полис на период проведения соревнования</li>
                    </ul>
                  </div>
                </section>

                <div className="border-t pt-4 mt-6">
                  <div className="grid grid-cols-2 gap-8">
                    <div>
                      <p className="font-bold text-xs mb-1">ОРГАНИЗАТОР</p>
                      <p className="text-xs text-gray-600 mb-2">Спортивная Федерация России</p>
                      <p className="text-xs text-gray-500">Подпись: ________________</p>
                    </div>
                    <div>
                      <p className="font-bold text-xs mb-1">УЧАСТНИК</p>
                      <p className="text-xs text-gray-600 mb-2">Иванов Иван Иванович</p>
                      <p className="text-xs text-gray-500">Подпись: ________________</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Signing Panel */}
        {showSigning && (
          <div className="w-80 bg-white rounded-r-xl flex flex-col">
            <div className="flex items-center justify-between px-6 py-4 border-b">
              <div>
                <h3 className="font-semibold">Подписание документа</h3>
                <p className="text-sm text-muted-foreground">Инструкция</p>
              </div>
              <Button variant="ghost" size="icon" onClick={handleClose}>
                <X className="h-5 w-5" />
              </Button>
            </div>

            <div className="flex-1 p-6 space-y-6">
              {/* Step 1 */}
              <div className="flex gap-4">
                <div className="flex-shrink-0 w-6 h-6 rounded-full bg-foreground text-background flex items-center justify-center text-sm font-medium">
                  1
                </div>
                <div className="flex-1">
                  <h4 className="font-medium mb-1">Скачайте документ</h4>
                  <p className="text-sm text-muted-foreground mb-3">
                    Нажмите кнопку ниже, чтобы скачать документ для подписания
                  </p>
                  <Button className="w-full bg-blue-500 hover:bg-blue-600 gap-2">
                    <Download className="h-4 w-4" />
                    Скачать документ
                  </Button>
                </div>
              </div>

              {/* Step 2 */}
              <div className="flex gap-4">
                <div className="flex-shrink-0 w-6 h-6 rounded-full bg-foreground text-background flex items-center justify-center text-sm font-medium">
                  2
                </div>
                <div className="flex-1">
                  <h4 className="font-medium mb-1">Подпишите документ</h4>
                  <p className="text-sm text-muted-foreground">
                    Используйте вашу электронную подпись для подписания скачанного документа
                  </p>
                </div>
              </div>

              {/* Step 3 */}
              <div className="flex gap-4">
                <div className="flex-shrink-0 w-6 h-6 rounded-full bg-foreground text-background flex items-center justify-center text-sm font-medium">
                  3
                </div>
                <div className="flex-1">
                  <h4 className="font-medium mb-1">Загрузите документ</h4>
                  <p className="text-sm text-muted-foreground mb-3">
                    Используйте вашу электронную подпись для подписания скачанного документа
                  </p>

                  {uploadState === "success" ? (
                    <div className="border-2 border-dashed border-emerald-200 rounded-lg p-6 bg-emerald-50 text-center">
                      <CheckCircle className="h-8 w-8 text-emerald-500 mx-auto mb-2" />
                      <p className="font-medium text-emerald-700">Документ загружен</p>
                      <p className="text-sm text-emerald-600">{uploadedFileName}</p>
                    </div>
                  ) : (
                    <label
                      className={cn(
                        "border-2 border-dashed rounded-lg p-6 text-center cursor-pointer transition-colors block",
                        isDragOver ? "border-blue-400 bg-blue-50" : "border-gray-200 hover:border-gray-300",
                      )}
                      onDragOver={handleDragOver}
                      onDragLeave={handleDragLeave}
                      onDrop={handleDrop}
                    >
                      <input
                        type="file"
                        className="hidden"
                        accept=".pdf,.doc,.docx,.jpg,.png"
                        onChange={handleFileSelect}
                      />
                      <Upload className="h-8 w-8 text-muted-foreground mx-auto mb-2" />
                      <p className="text-sm font-medium">Выберите или перетащите файл сюда</p>
                      <p className="text-xs text-muted-foreground mt-1">PDF, DOC, DOCX, JPG, PNG до 10 МБ</p>
                    </label>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
